export default {
  data() {
    let datas = this.$store.state.msgList.baseMsg;
    let mid = +this.$route.query.mid;
    let msgData;

    datas.forEach(e => {
      if (e.mid === mid) {
        msgData = e;
      }
    });
    return {
      msgData
    };
  }
};



